}

:root {
  --dark-light: #353746;
  --grey: #ecedf3;
}

p {
  color: white;
}

.over-hide {
  overflow: hidden;
}

[type="checkbox"]:checked,
[type="checkbox"]:not(:checked),
[type="radio"]:checked,
[type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px;
  width: 0;
  height: 0;
  visibility: hidden;
}

.checkbox-budget:checked + label,
.checkbox-budget:not(:checked) + label {
  position: relative;
  display: inline-block;
  padding: 10px 2px;
  width: 300px;
  font-size: 35px;
  line-height: 52px;
  font-weight: 680;
  letter-spacing: 1px;
  margin: 1.5em 15px 0em;
  text-align: center;
  border-radius: 5px;
  overflow: hidden;
  cursor: pointer;
  text-transform: uppercase;
  -webkit-transition: all 300ms linear;
  transition: all 300ms linear;
  -webkit-text-stroke: 1px white;
  -webkit-text-fill-color: transparent;
  color: transparent;
}

.checkbox-budget:not(:checked) + label {
  background-color: var(--dark-light);
  -webkit-box-shadow: 0 2px 4px 0 var(--dark-light);
          box-shadow: 0 2px 4px 0 var(--dark-light);
}

.checkbox-budget:checked + label {
  background-color: transparent;
  -webkit-box-shadow: 0 8px 16px 0 var(--dark-light);
          box-shadow: 0 8px 16px 0 var(--dark-light);
}

.checkbox-budget:not(:checked) + label:hover {
  -webkit-box-shadow: 0 8px 10px 0 #cc7a4a65;
          box-shadow: 0 8px 10px 0 #cc7a4a65;
}

.checkbox-budget:checked + label::before,
.checkbox-budget:not(:checked) + label::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 4px;
  background-image: linear-gradient(138deg, #cf4759a8, #df9062e3);
  z-index: -1;
}

.checkbox-budget:checked + label span,
.checkbox-budget:not(:checked) + label span {
  position: relative;
  display: block;
}

.checkbox-budget:checked + label span::before,
.checkbox-budget:not(:checked) + label span::before {
  position: absolute;
  content: attr(data-hover);
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  -webkit-text-stroke: transparent;
  -webkit-text-fill-color: var(--white);
  color: var(--white);
  -webkit-transition: max-height 0.3s;
  transition: max-height 0.3s;
}

.checkbox-budget:not(:checked) + label span::before {
  max-height: 0;
}

.checkbox-budget:checked + label span::before {
  max-height: 100%;
}
